.form {
  box-sizing: border-box;
  width: 320px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: auto;

}
.label {
  width: 286px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  color: var(--grey);
  text-align: left;
  

}
.input {
  box-sizing: border-box;
  width: 286px;
  border-radius: 3px;
  border: 1px solid var(--grey);
  color: var(--grey);
  margin-top: 0px;
  margin-bottom: 6px;
  font-size: 14px;
  padding-left: 6px;

}
.input:is(:hover, :focus),
.input.active {
  outline: 1px solid var(--greyActive);
}

.btn {
    position: absolute;
    top: 43%;
    transform: translate(-50%);
    right: -6px;
    background-color: var(--green);
    color: var(--white);
}
.btn:is(:hover, :focus) {
  background-color: var(--greenActive);
}